<template>
  <v-navigation-drawer
    elevation="1"
    :modelValue="drawer"
    @update:modelValue="(a) => $emit('update:drawer', a)"
    class="menu menu-gradient"
  >
    <v-list density="comfortable" :opened="opened">
      <v-list-item title="首頁" to="/home/index" prepend-icon="mdi-home" exact></v-list-item>
      <item v-for="a in list" :item="a"></item>
    </v-list>
  </v-navigation-drawer>
</template>

<script setup>
import item from "./item";
import { ref, onMounted, watch, inject } from "vue";
import { useRoute } from "vue-router";

const list = ref([]);
const route = useRoute();
const root = inject("root");
defineProps(["drawer"]);
const emit = defineEmits(["update", "update:drawer"]);
const opened = ref([]);

const load = (reset) => {
  root.functions(reset).then((a) => {
    list.value = [...a.data];
    update();
  });
};

const update = () => {
  let name = route.name || "/";
  let deep = 0;
  let items = [];
  let flag = true;
  // let pages = [];
  let operations = [];
  opened.value = [];

  (function x(a, i) {
    if (!a) return false;
    let result = false;
    a.forEach((b) => {
      if (["operation", "api"].includes(b.type)) return false;
      b.isActive = false;
      if (b.url == name || x(b.children, i + 1)) {
        result = true;
        if (deep === 0) root.subTitle.value = b.text;
        if (deep === 1) root.title.value = b.text;
        deep++;
        b.isActive = true;
        if (flag) items.push(b);
        if (i == 0) flag = false;
        if (operations.length < 1)
          operations = b.children.filter((c) => c.type == "operation");
        opened.value.push(b.id);
      }
    });
    return result;
  })(list.value, 0);
  root.operations = operations;
  emit("update", items.reverse());
};

onMounted(() => load(true));

watch(route, update);
defineExpose({ load });
</script>

<style scoped>
.menu-gradient {
  color: white;
  background: linear-gradient(
      to bottom,
      rgba(30, 51, 72,.3) 0%,
      rgba(72, 123, 174,.3) 50%,
      rgba(30, 51, 72,.3) 100%
    ),rgb(30, 51, 72);
}
:deep(.v-list-item__spacer) {
  width: 8px !important;
}
</style>
