<template>
  <v-list-item :value="item.id" v-if="item.type == 'page' && item.url" :title="item.text" :to="item.url" :prepend-icon="item.icon || 'mdi-arrow-right-bold'" :active="item.isActive"></v-list-item>
  <v-list-group v-if="item.type == 'menu'" ref="group" :value="item.id">
    <template v-slot:activator="{ props }">
      <v-list-item v-bind="props" :prepend-icon="item.icon || 'mdi-folder'" :title="item.text"></v-list-item>
    </template>
    <node v-for="a in item.children" :item="a"></node>
  </v-list-group>
</template>

<script setup>
defineOptions({ name: 'node' })
defineProps(['item'])
</script>

<style scoped>
:deep(.v-list-item__spacer) {
  width: 8px !important;
}
</style>